import axios from 'axios';
import { signOut } from "next-auth/react";

const Axios = axios.create({
    baseURL: typeof window !== "undefined" ? window.location.origin : process.env.NEXT_PUBLIC_API_DIR_PATH,
    headers: {
        'Content-Type': 'application/json',
    },
    withCredentials: true,
});

Axios.interceptors.response.use(
    r => r,
    async (error) => {
        if (error.response?.status === 401) {
            await signOut({ callbackUrl: "/auth/login" });
        }
        return Promise.reject(error);
    }
);

async function postData(endpoint: string, data = {}, customHeader = {}) {
    try {
        const timestamp = new Date().toISOString();
        const header = {
            'X-ENDPOINT': endpoint,
            'X-Timestamp': timestamp,
            'X-Custom-Header': JSON.stringify({
                'X-Level': "1",
                ...customHeader
            }),
        };

        const response = await Axios.post('/api/bridge/login', data, {
            headers: header,
        });
        
        return response;
    } catch (error: any) {
        throw error;
    }
}

export default postData;
